<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')">자주묻는질문</li>
			<li @click="goto('/admin/terms')" class="current">약관</li>
		</ul>
		<LoadingSpinner :loading="isLoading" />
		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop index">No.</div>
						<div class="prop justify-flex-start" style="width: 725px">
							약관 이름
						</div>
						<div class="prop ">최종수정일자</div>
						<div class="prop">수정 및 보기</div>
					</li>
					<slot v-if="!termList">
						<li class="row no-data body3" style="display: block;">
							등록된 글이 없습니다
						</li>
					</slot>
					<slot v-else>
						<li v-for="item in termList" :key="item.id" class="row">
							<div class="prop index">{{ item.id }}</div>
							<div class="prop justify-flex-start" style="width: 725px">
								{{ item.title }}
							</div>
							<div class="prop">
								{{ $moment(item.updateDate).format('YYYY.MM.DD') }}
							</div>
							<div class="prop multiple-icons">
								<router-link
									:to="{
										name: 'EditTermsContent',
										params: { title: item.title },
									}"
								>
									<Edit />
								</router-link>
								<Monitor></Monitor>
							</div>
						</li>
					</slot>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import { fetchTerm } from '@/api/terms';
import Monitor from '@/assets/icon/Monitor';
import Edit from '@/assets/icon/Edit';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner, Edit, Monitor },
	created() {
		this.fetchTerm();
	},
	data() {
		return {
			termList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
		};
	},
	computed: {
		selectAll: {
			get: function() {
				return this.TermList
					? this.selected.length == this.TermList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.TermList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchTerm() {
			this.isLoading = true;
			try {
				const { data } = await fetchTerm();
				this.termList = data;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchTerm(this.currentPage - 1);
			}
		},
	},
};
</script>

<style></style>
